import React, { useState } from 'react'

import Card from '../styles/Card'
import { StandardText, BoldText, H2, H3 } from '../styles/Typography'
import Button from '../styles/Button'
import ProjectsSection from '../styles/ProjectsSection'

import mmpLogo from '../assets/mmpLogo.png'
import mmpCover from '../assets/mmpCover.jpg'
import ccLogo from '../assets/ccLogo.png'
import ccCover from '../assets/ccCover.jpg'

const Project = (props) => {
  return (
    <Card>
      <img
        src={props.cover}
        alt={props.name}
        onClick={() => { window.open(props.website) }}
        style={{ cursor: 'pointer' }}
      />
      <div>
        <img src={props.logo} alt={props.name} />
        <H3>{props.name}</H3>
      </div>
      <StandardText>
        {props.about}
      </StandardText>
      <br />
      <BoldText>
        {props.technologies}
      </BoldText>
      <Button primary fontColor={props.secondaryColor} backgroundColor={props.primaryColor} onClick={() => { window.open(props.website) }}>Ir para o site</Button>
      <Button backgroundColor={props.primaryColor} fontColor={props.primaryColor} onClick={() => { window.open(props.github) }}>GitHub</Button>
    </Card>
  )
}

const MyProjects = (props) => {
  const [projects, setProjects] = useState([
    {
      id: 0,
      name: 'My Musical Picture',
      about: 'O My Musical Picture  uma aplicao web que faz uma imagem baseada no histrico de msicas ouvidas pelo usurio no Spotify. Foi usado a API do Spotify para pegar os dados, alm das bibliotecas FileSaver e DomToImage para gerar a imagem.',
      technologies: 'ReactJS, Gatsby, NodeJS, Express, Spotify Web API, Styled Components',
      website: 'https://my-musical-picture.netlify.app/',
      github: 'https://github.com/naymello/my-musical-picture',
      logo: mmpLogo,
      cover: mmpCover,
      primaryColor: '#2929B1',
      secondaryColor: '#F9FF3E'
    },
    {
      id: 1,
      name: 'Chord Chart',
      about: 'O Chord Chart  uma aplicao web que mostra acordes do piano de forma dinmica, facilitando o aprendizado no instrumento. Para isso, foi usado a manipulao de SVG (Scalable Vector Graphics) com a biblioteca React SVG Manipulation Tools.',
      technologies: 'ReactJS, Create React App (CRA), Styled Components',
      website: 'https://chord-chart.netlify.app/',
      github: 'https://github.com/naymello/chord-chart',
      logo: ccLogo,
      cover: ccCover,
      primaryColor: '#001724',
      secondaryColor: '#8BC9E5'
    }
  ])

  return (
    <div ref={props.reference} style={{ opacity: 0, transform: 'translate(0, 60px)' }}>
      <H2 color="#FF9FFB" id="projetos">Meus projetos</H2>
      <ProjectsSection>
        {projects.map((project) => (
          <Project
            key={project.id}
            name={project.name}
            about={project.about}
            technologies={project.technologies}
            website={project.website}
            github={project.github}
            logo={project.logo}
            cover={project.cover}
            primaryColor={project.primaryColor}
            secondaryColor={project.secondaryColor}
          />
        ))}
      </ProjectsSection>
    </div>
  )
}

export default MyProjects
